@import url(https://fonts.googleapis.com/css?family=Oswald:700&display=swap);
.section {
  margin: auto;
  margin-top: 10em;
  max-width: 70%;
  align-content: left;
  justify-content: center;
  min-height: 25em;
}

#App {
  height: 100vh;
}

h1 {
  text-transform: uppercase;
  color: #4e4232;
}

h4 {
  color: #ff4141;
}

body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
      "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
      sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background-color: #fffff9 ;
  }

p {
    color: #7c7c7c;
    font-size: large;
}

a {
  color: #ff4141;
  text-decoration: none;
  font-weight: bold;
}

a:hover {
  /* color: #71bfec; */
  color: #8d2727;
  text-decoration: underline;
}

@media only screen and (max-width: 600px) {
  .section {
    max-width: 100%;
    margin-top: 2em;
  }

  .section h1 {
    margin-left: 0.5em;
  }
}
#me {
    border-radius: 50%;
    max-width: 12em;
}

#about {
    margin-top: 5em;
}

#about h1 {
    text-transform: uppercase;
    font-family: 'Oswald', Impacto, Impact, sans-serif, 'Arial Narrow Bold', sans-serif;
    font-weight: normal;
    font-size: 3.5em;
}

#about .icons a {
    margin: 0px 10px 0px 6px;
    padding: 0px 5px 0px 5px;
}

#first {
    color: #ff4141;
}

#email {
    font-size: 3.5em;
    color: #ff4141;
}

#email:hover {
    color: #8d2727;
}

.icons {
    font-size: 3.8em;
    padding: 0.05em;
    margin: 0.05em;
}

.icons a {
    color: #888888;
}

.icons a:hover {
    color: #ff4141;
}

@media only screen and (max-width: 600px) {
    #about {
        padding: 1em;
    }
    #about h1 {
        font-weight: bold;
        margin-left: inherit;
        font-size: 3em;
        text-align: center;
    }
    #email {
        display: block;
        margin: 0 auto;
    }
    .icons {
        margin-top: 1em;
        text-align: center;
    }
    #me {
        display: block;
        margin: 0 auto;
    }
}
#fa-link {
    position: relative;
    right: 1.6em;
    top: 1.9em;
}

/* 
direct child selectors are present because I don't want to lose the underline
on links in the paragraph, just its headers. */

.project>a, .job>a, .source-link {
    text-decoration: none;
}

#experience {
    padding-top: 2em;
    padding-bottom: 2em;
}

#projects {
    margin-top: 10em;
}

#professional h1, #projects h1 {
    font-size: 2.5em;
}

#projects div, #professional div {
    box-shadow: 1em 1em #0000005e;
    padding: 10%;
}

.job h2, .job h3, .project h2, .project h3 {
    text-transform: uppercase;
}

.job h2, .project h2 {
    font-size: 1.7em;
    margin-bottom: 0;
    margin-top: 0;
}

.job h3, .project h3 {
    margin-top: 0.1em;
}

.job:not(:first-of-type) {
    margin-top: 5em;
}

.project {
    margin-bottom: 5em;
}

#geoswap {
    background-color: darkslategray;
}

#geoswap h4, #geoswap a {
    color: coral;
}

#geoswap a:hover {
    color: white;
}

#geoswap p {
    color: lightgray;
}

#estuary-escapade {
    background-color: #6dbfe6;
}

#estuary-escapade h4, #estuary-escapade a {
    color: #1944ff;
}

#estuary-escapade a:hover {
    color: #142677;
}

#estuary-escapade p {
    color: #49385a;
}

#grenzy {
    background-color: #FFD8BE;
}

#grenzy h4, #grenzy a {
    color: #2E7E6E;
}

#grenzy a:hover {
    color: #186354;
}

#grenzy p {
    color: #203C66;
}

#weather {
    background-color: black;
}

#weather a, #weather h4 {
    color: white;
}

#weather a:hover, #weather p {
    color: lightgray;
}

#to-do {
    background-color: yellow;
}

#to-do a, #to-do h4 {
    color: black;
}

#to-do a:hover {
    color: red;
}

#to-do p {
    color: darkslategray;
}

.source-link {
    position: relative;
    top: -8%;
    font-size: 100%;
}

#tic-tac-toe {
    background-color: #D8E3A7;
}

#tic-tac-toe a, #tic-tac-toe h4 {
    color: #535B37;
}

#tic-tac-toe a:hover {
    color: #ff4141;
}

#tic-tac-toe p {
    color: #323532;
}

#calculator {
    background-color: #1B1B1B;
}

#calculator a, #calculator h4 {
    color: #949292;
}

#calculator a:hover {
    color: #535151;
}

#etch-a-sketch {
    background-color: lavender;
}

#etch-a-sketch a, #etch-a-sketch h4 {
    color: red;
}

#etch-a-sketch a:hover {
    color: black;
}

@media only screen and (max-width: 600px) {
    #fa-link {
        right: 0;
        top: 0;
    }
    #experience {
        padding-bottom: 0;
        margin-top: 0;
    }
    #projects div, #professional div {
        box-shadow: none;
        padding: 5%;
    }
    div#projects, div#professional {
        margin: 0;
    }
    .job, .project {
        margin-top: 0;
        margin-bottom: 0;
    }
}
#education .school {
    padding: 10%;
    box-shadow: 1em 1em #0000005e;
    margin-bottom: 5em;
}

.school h5 {
    color: #dadada;
}

#ud {
    background-color: #1957ff;
}

#ud h2, #ud h3, #ud p {
    color: #ffe607;
}

#umuc {
    background-color: #CE0E2D;
}

#umuc h2, #umuc h3, #umuc p {
    color: #ffe607;
}

@media only screen and (max-width: 600px) {
    #education .school {
        box-shadow: none;
        padding-left: 5%;
        padding-right: 5%;
        margin-bottom: 0;
    }
}
.languages #language-child {
    font-size: 45px;
    color: #e4e4e4;
    margin: 6px;
}

.languages #language-child:hover {
    color: #71bfec;
}

.languages span p {
    color: white;
}

.languages {
    display: grid;
    grid-template-columns: 6vw 6vw 6vw 6vw;
    grid-template-rows: 11vh 11vh;
    grid-column-gap: 11vh;
    -webkit-column-gap: 11vh;
            column-gap: 11vh;
    grid-row-gap: 7vh;
    row-gap: 7vh;
    margin-left: 9vw;
}

#terminal {
    background-color: #e4e4e4;
    color: #ff4141;
    font-size: 30px;
    padding: 5px;
}

#terminal:hover {
    background-color: #71bfec;
}

#skills-box {
    background-color: #ff4141;
    padding: 10%;
    box-shadow: 1em 1em #0000005e;
}

#skills-box h2, #skills-box h3 {
    color: #423f3f;
}

#skills {
    margin-bottom: 5%;
}

/* Portrait phone */

@media only screen and (max-width: 600px) {
    #skills-box {
        box-shadow: none;
        padding-left: 5%;
        padding-right: 5%;
    }
    #skills {
        margin-bottom: 0;
    }
    .languages {
        grid-template-rows: 60px 60px 60px 60px;
        grid-template-columns: 100px 100px;
        grid-row-gap: 50px;
        row-gap: 50px;
        grid-column-gap: 30px;
        -webkit-column-gap: 30px;
                column-gap: 30px;
        padding-bottom: 50px;
        margin-left: 20%;
    }
}

/* ipad in landscape */

@media only screen and (min-device-width: 768px) and (max-device-width: 1024px) and (orientation: landscape) {
    .languages {
        grid-template-rows: 7vw 7vw;
        grid-template-columns: 5vw 5vw 5vw 5vw;
        grid-column-gap: 10vw;
        -webkit-column-gap: 10vw;
                column-gap: 10vw;
        grid-row-gap: 14vh;
        row-gap: 14vh;
        margin-left: 3vw;
    }
}

/* iPad in portrait */

@media only screen and (min-device-width: 768px) and (max-device-width: 1024px) and (orientation: portrait) {
    .languages {
        grid-template-columns: 5vw 5vw 5vw 5vw;
        grid-template-rows: 11vh 11vh;
        grid-column-gap: 10vw;
        -webkit-column-gap: 10vw;
                column-gap: 10vw;
        grid-row-gap: 7vh;
        row-gap: 7vh;
        margin-left: 2vw;
    }
}
#interests-box {
    background-color: #616161;
    padding: 10%;
    box-shadow: 1em 1em #0000005e;
}

#interests {
    margin-bottom: 10em;
}

#interests p {
    color: #dbdada;
}

@media only screen and (max-width: 600px) {
    #interests-box {
        box-shadow: none;
        padding-top: 5em;
        padding-bottom: 5em;
        padding-left: 5%;
        padding-right:5%;
    }

    #interests {
        margin-bottom: 0;
    }
}
#page-wrap {
    overflow: auto;
}

.bm-item {
    display: inline-block;
    transition: color 0.2s;

    text-decoration: none;
    margin-bottom: 10px;
    color: white;
}

.bm-item:hover {
    color: #f1b1b1;
    text-decoration: none;
}

/* Position and sizing of burger button */
.bm-burger-button {
    position: fixed;
    width: 36px;
    height: 30px;
    left: 36px;
    top: 36px;
  }
  
  /* Color/shape of burger icon bars */
  .bm-burger-bars {
    background: #ff4141;
  }
  
  /* Position and sizing of clickable cross button */
  .bm-cross-button {
    height: 24px;
    width: 24px;
  }
  
  /* Color/shape of close button cross */
  .bm-cross {
    background: white;
  }
  
  /* General sidebar styles */
  .bm-menu {
    background: #ff4141;
    padding: 2.5em 1.5em 0;
    font-size: 1.15em;
  }
  
  /* Morph shape necessary with bubble or elastic */
  .bm-morph-shape {
    fill: #ff4141;
  }
  
  /* Wrapper for item list */
  .bm-item-list {
    color: #b8b7ad;
  }
  
  /* Styling of overlay */
  .bm-overlay {
    background: #0000004d;
  }

  @media only screen and (max-width: 600px) {
    .bm-burger-bars{
      box-shadow: 0em 0em 0em 0.15em #fffff991;
      background: #ff414191;
    }
  }
#mini {
    font-size: 3em;
    text-align: center;
    padding: 0.4em;
    margin-bottom: 10%;
}

#mini a {
    padding: inherit;
}

#mini p {
    font-size: 0.8rem;
}

@media only screen and (max-width: 600px) {
    #footer {
        padding-top: 5%;
        background: #383838;
    }
    #mini {
        margin-bottom: 0;
        padding-bottom: 10%;
    }
    #mini p {
        font-size: 1rem;
    }
}
