@import url('https://fonts.googleapis.com/css?family=Oswald:700&display=swap');
#me {
    border-radius: 50%;
    max-width: 12em;
}

#about {
    margin-top: 5em;
}

#about h1 {
    text-transform: uppercase;
    font-family: 'Oswald', Impacto, Impact, sans-serif, 'Arial Narrow Bold', sans-serif;
    font-weight: normal;
    font-size: 3.5em;
}

#about .icons a {
    margin: 0px 10px 0px 6px;
    padding: 0px 5px 0px 5px;
}

#first {
    color: #ff4141;
}

#email {
    font-size: 3.5em;
    color: #ff4141;
}

#email:hover {
    color: #8d2727;
}

.icons {
    font-size: 3.8em;
    padding: 0.05em;
    margin: 0.05em;
}

.icons a {
    color: #888888;
}

.icons a:hover {
    color: #ff4141;
}

@media only screen and (max-width: 600px) {
    #about {
        padding: 1em;
    }
    #about h1 {
        font-weight: bold;
        margin-left: inherit;
        font-size: 3em;
        text-align: center;
    }
    #email {
        display: block;
        margin: 0 auto;
    }
    .icons {
        margin-top: 1em;
        text-align: center;
    }
    #me {
        display: block;
        margin: 0 auto;
    }
}